<template>
  <div
    :class="$root.themeHomeChange === '1' ? 'operationLog_light' : 'operationLog'"
    id="operationLog">
    <div class="header">
      <span style="font-weight: bold;">操作日志</span>
      <div style="font-size: 12px; font-weight: 500;">
        <el-button
          type="success"
          icon="el-icon-printer"
          :plain="$root.themeHomeChange === '1'"
          @click="exportExcel"
          :loading="exportLoading"
        > 导出日志 </el-button>
      </div>
    </div>
    <el-form
      :inline="true"
      class="selectBox"
      :model="params">
      <!-- <el-button type="primary" style="float: left;">生成报告</el-button> -->
      <span>
        <el-form-item label="操作时间: " prop="operationDate">
          <el-date-picker
            v-model="params.operationDate"
            size="mini"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
          ></el-date-picker>
        </el-form-item>
      </span>
      <span style="float: right;">
        <el-form-item>
          <bda-search-input
            v-model="params.keyword"
            placeholder="请输入关键字"
            @search="getTableList(true)"
            @clear="getTableList(true)"
            size="mini"
          >
          </bda-search-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="getTableList(true)"
          > 查询 </el-button>
        </el-form-item>
      </span>
    </el-form>
    <div class="content">
      <el-table
        ref="multipleTable"
        :data="tableData"
        style="width: 100%;"
        tooltip-effect="light"
        :height="tableHeight"
        @selection-change="handleSelectionChange"
        :row-key="rowKeyInit"
        v-loading="tableLoading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
       <!-- <el-table-column
          type="selection"
          :selectable="selectInit"
          reserve-selection
          width="55"
          align="center">
        </el-table-column> -->
        <el-table-column
          prop="operId"
          label="日志编号"
          width="80"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="title"
          label="系统模块"
          min-width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="operatorType"
          label="操作类型"
          min-width="80"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ tableOption['type'][scope.row.operatorType] }}
          </template>
        </el-table-column>
        <el-table-column
          prop="operatorDesc"
          label="操作说明"
          min-width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="operName"
          label="操作人员"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="operIp"
          label="IP地址"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="deptName"
          label="所属部门"
          min-width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="status"
          label="操作状态"
          width="85"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <span
              :style="{
                cursor: 'pointer',
                color: (!!scope.row.status ? '#67C23A' : '#e63d1b'),
                'font-weight': 'bold'
              }"
              >
              <!-- @click="!!scope.row.defectStatus ? () => {} : detailDialogClick()" -->
              {{ !!scope.row.status ? "正常" : "异常" }}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="operTime"
          label="操作时间"
          width="160"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="params.pageSize"
        background
        layout="prev, pager, next, sizes, jumper, total"
        :total="params.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Interface from "@/base/mixin/system-log.js";
  import {API_PATH} from "@/config"
  import axios from "axios";
  // import Qs from "qs";
  export default {
    name: "operationLog",
    components: { },
    mixins: [Interface],
    props: {
      orderId: {
        type: String,
        default: () => {
          return ''
        }
      }
    },
    data() {
      return {
        exportLoading: false,
        tableHeight: window.innerHeight - 240,
        detailInfo: [], // 异常详细信息
        params: {
          pageSize: 10,
          currentPage: 1,
          total: 0,
          operationDate: []
        },
        defectStatus: '',
        multipleSelection: [], // 多选
        tableLoading: false,
        tableOption: {
          type: {
            0: '其它',
            1: '新增',
            2: '修改',
            3: '删除',
            4: '授权',
            5: '上传',
            6: '下载',
            7: '导出',
            8: '导入',
            9: '强退',
            10: '生成代码',
            11: '清空数据',
          }
        },
        tableData: []
      }
    },
    watch: {
      'params.operationDate' : {
        handler(v) {
          if(v === null) {
            this.params.operationDate = [];
          }
        },
        immediate: true
      }
    },
    created() {
      this.initData();
    },
    mounted() {},
    methods: {
      // 初始化数据
      initData() {
        this.getTableList();
      },
      // row-key定义
      rowKeyInit(row) {
        return row.defectId;
      },
      // 获取table表格数据
      getTableList(reset) {
        if(reset) {
          this.setPages();
        }
        let params = {
          defectStatus: this.params.defectStatus,
          current: this.params.currentPage,
          size: this.params.pageSize,
          beginTime: this.params.operationDate[0],
          endTime: this.params.operationDate[1],
          keyword: this.params.keyword,
          mark: "",
        };
        this.tableData = [];
        this.tableLoading = true;
        this.getOperlogList(params).then((rs)=>{
          // window.console.log(rs)
          if(rs) {
            this.tableData = rs.rows;
            this.setPages(rs.current, rs.size, rs.records);
          } else {
            this.setPages();
          }
        }).finally(()=>{
          this.tableLoading = false;
        })
      },
      toggleSelection(defectIds) {
        if(defectIds) {
          defectIds.forEach(id => {
            window.console.log(id, this.tableData)
            this.$refs.multipleTable.toggleRowSelection(
              this.tableData.find(item => {
                return item.defectId === id
              })
            );
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      // 设置页码
      setPages(currentPage = 1, pageSize = 10, total = 0) {
        this.params['pageSize'] = pageSize;
        this.params['currentPage'] = currentPage;
        this.params['total'] = total;
      },
      // 搜索对应内容
      searchTableNames(arr) {
        let title = '';
        arr.forEach(item_1 => {
          title += this.tableData.find(item_2 => item_2.defectId == item_1.defectId).ruleName + ',';
        });
        return title;
      },
      handleSizeChange(val) {
        // window.console.log(`每页 ${val} 条`);
        this.params.currentPage = 1;
        this.params.pageSize = val;
        this.getTableList();
      },
      handleCurrentChange(val) {
        // window.console.log(`当前页: ${val}`);
        this.params.currentPage = val;
        this.getTableList();
      },
      // 多选
      handleSelectionChange(val) {
        this.multipleSelection = val;
        // this.$emit('selectChange', val.map(item => {
        //   return {
        //     id: item.defectId,
        //     defectStatus: item.defectStatus,
        //   }
        // }))
      },
      selectInit(row) {
        // console.log(row, index)
        let judge = true;
        if(this.multipleSelection.length != 0) {
          judge = this.multipleSelection.some(item => {
            if(item.defectStatus === row.defectStatus && row.defectStatus !== 5) {
              return true;
            }
          })
        }
        return judge;
      },
      //导出excel
      exportExcel() {
        let path = '/operlog/export';
        let data = {
          current: this.params.currentPage,
          size: this.params.pageSize,
          beginTime: this.params.operationDate[0],
          endTime: this.params.operationDate[1],
          keyword: this.params.keyword,
          mark: '',
        };
        this.exportLoading = true;
        axios({
          method: "POST",
          url: API_PATH + path,
          data:data,
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          responseType: 'blob'
        }).then(res => {
          const content = res.data
          const blob = new Blob([content])
          const fileName = '操作日志.xlsx'
          if ('download' in document.createElement('a')) { // 非IE下载
            const elink = document.createElement('a')
            elink.download = fileName
            elink.style.display = 'none'
            elink.href = URL.createObjectURL(blob)
            document.body.appendChild(elink)
            elink.click()
            URL.revokeObjectURL(elink.href) // 释放URL 对象
            document.body.removeChild(elink)
          } else { // IE10+下载
            navigator.msSaveBlob(blob, fileName)
          }
        }).finally(()=>{
          this.exportLoading = false;
        })
        .catch(()=>{})
      },
    }
  }
</script>

