/**
 *  系统日志 mixin module
 *  @module base/mixin/systemLog
 *  @author coder
 */
import {
  getOperlogList,
  getLoginlogList
} from '@/base/api/system-log'

// mixin
export default {

  /**
   * 系统日志数据存储载体对象
   * @member systemLog
   * @property {Array} [list=[]] 获取系统日志列表响应数据
   * @property {number} [page=null] 获取系统日志列表响应页码
   * @property {number} [limit=null] 获取系统日志列表页大小，每页记录条数
   * @property {number} [total=0] 获取系统日志列表数据记录总条数
   * @property {object} [model=null] 获取系统日志单条记录
   */
  data() {
    return {
      systemLog: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 操作日志-获取列表
     * @function getOperlogList
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getOperlogList(data) {
      return getOperlogList(data).then(res => {
        return res
      })
    },
    /**
     * 操作日志-获取列表
     * @function getLoginlogList
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getLoginlogList(data) {
      return getLoginlogList(data).then(res => {
        return res
      })
    }
  }
}
