// API_PATH
import {
  API_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 操作日志-获取列表
 * @const
 * @type {String}
 * @default  '[API_PATH]/operlog/list'
 */
export const GET_OPERLOG_LIST = API_PATH + '/operlog/list'

/**
 * 操作日志-获取列表
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getOperlogList(data) {
  return ajax({
    method: 'POST',
    headers: {
      "Content-Type": "application/json;charset=utf8"
    },
    data: data,
    url: GET_OPERLOG_LIST
  })
}

